import nodata from "@/components/nodata.vue";
export default {
	components: {
		nodata
	},
	data() {
		return {
			price: '',
			sureprice: 0,
			inputtxt: '', //输入框的提示信息
			withdrawallist: []
		};
	},
	watch: {
		// 输入框提示语
		sureprice(val) {
			this.inputtxt = `可提现金额为:${val}元`
		}
	},

	methods: {
		// 立即提现
		async withdrawal() {

			// 先判断不能为空
			if (!this.price.length) {
				uni.showToast({
					title: '提现金额不能为空!',
					duration: 2000,
					icon: "none"
				})
				return false
			}
			if (this.price.substring(0, 1) === '.' || parseFloat(this.price) < 1) {
				uni.showToast({
					title: '最低提现一元!',
					duration: 2000,
					icon: "none"
				})
				return false
			}
			// 判断提现金额是否大于可提现金额
			if (parseFloat(this.price) > parseFloat(this.sureprice)) {
				uni.showToast({
					title: '提现金额不能大于可提现金额',
					duration: 2000,
					icon: "none"
				})
				return false
			}
			uni.showLoading({
				title: '提现中..',
				mask: true
			})
			// 通过检测
			let applyWithdrawal = await this.ajax(this.api.applyWithdrawal, {
				price: this.price
			}, 'POST')
			uni.hideLoading()
			if (applyWithdrawal.data.code) {
				uni.showToast({
					title: applyWithdrawal.data.msg,
					duration: 2000,
					icon: "none"
				})
			}
			this.price = ''
			
			// 获取当前的金额
			this.getnowMoney()
			this.getwithdrawallist()
		},

		//获取当前金额
		async getnowMoney() {
			let nowMoney = await this.ajax(this.api.nowMoney)
			if (nowMoney.data.code == 200) {
				if (nowMoney.data.data != 'null') {
					this.sureprice = nowMoney.data.data
				}
			}
		},

		//获取提现列表
		async getwithdrawallist() {
			let withdrawal = await this.ajax(this.api.withdrawal)
			if (withdrawal.data.code == 200) {
				if (withdrawal.data.data != 'null') {
					this.withdrawallist = withdrawal.data.data
				}
			}

		},
	},
	async onLoad() {

		this.getnowMoney()

		//获取提现记录
		this.getwithdrawallist()
	},

	onShow() {

		this.sureprice = 88
	}
}
